import Vue from "vue";
import Vuex from 'vuex';
import App from "./App.vue";
import { createRouter } from "./router";
import { createStore } from "./vuex/store";
import { sync } from "vuex-router-sync";
//  router  store 
Vue.use(Vuex);
const router = createRouter();
const store = createStore();
// (route state) store
sync(store, router);
//  router  store 
const app = new Vue({
  router,
  store,
  el: "#app",
  render: h => h(App)
});



